<template>
	<view class="body">
		<!-- 头部模块 -->
		<view class="head">
			<view class="word" @click="toNewWord">
				<img src="../../static/langStatic/wordBook.png">
				<text>单词本</text>
			</view>
			<view class="head_title">Text4，2008</view>
			<view class="difficulty">
				<view>难度</view>
				<u-icon name="star-fill" color="#ffd75c" size="28rpx"></u-icon>
				<u-icon name="star-fill" color="#ffd75c" size="28rpx"></u-icon>
				<u-icon name="star-fill" color="#e3e3e3" size="28rpx"></u-icon>
			</view>
		</view>
		<!-- 句子 -->
		<view class="sentence" v-html="data.subject"></view>
		<!-- 单元 -->
		<view class="title">
			<view class="number">{{data.index}}</view>
			<view class="text">{{data.title}}</view>
		</view>
		<!-- 答案 -->
		<view v-if="isSee==false" class="answer">
			<view class="cell sortInput" v-for="(item,index) in data.option" :key="index">
				<view>{{item.key}}: </view>
				<input class="inputs" type="text" v-model="result[index]" />
			</view>
			<button @click="seeResolution">查看解析</button>
		</view>
		<!-- 答案 -->
		<view v-else class="answer">
			<view class="cell sortInput" v-for="(item,index) in data.option_answer" :key="index">
				<view>{{item.key}}: </view>
				<input v-if="item.answer==item.value" class="inputs trueAnswers" type="text" v-model="item.answer" :disabled="true"/>
				<input v-if="item.flag=='false'" class="inputs falseAnswer" type="text" v-model="item.answer" :disabled="true"/>
				<view v-if="item.flag=='false'" class="input trueAnswer">{{item.value}}</view>
				<!-- <input v-if="result[index]!=item.value" class="inputs trueAnswer" type="text" v-model="item.value" :disabled="true"/> -->
			</view>
			<button>查看解析</button>
		</view>
		
		<view class="tips">注: 输入内容中有多余空格、错误标点符号、拼写错误等皆不正确。</view>
		<!-- 答案解析 -->
		<view v-show="isSee" class="answerAnalysis">
			<view class="title">答案解析</view>
			<view class="content" v-html="data.analysis"></view>
		</view>
		<!-- 视频 -->
		<view v-if="data.video" v-show="isSee" class="answerAnalysis">
			<view class="title">教你拆解法的短视频</view>
			<video :src="data.video" id="videos" class="videos" controls @click="closeSpeedList" object-fit="cover">
				<cover-view class="video-control">
					<cover-view class="multi rate" @tap="showSwitchRate">X {{currentRate}}</cover-view>
				</cover-view>
				<cover-view class="multi-list rate" :class="{ active: rateShow }">
					<cover-view
						v-for="(item, index) in ['0.5', '1.0', '1.5', '2.0']"
						:key="index"
						class="multi-item rate"
						:data-rate="item"
						@tap="switchRate"
						:class="{ active: item == currentRate }"
					>
						{{ item }}
					</cover-view>
				</cover-view>
			</video>
		</view>
		<!-- 下一步 -->
		<button @click="toNext">下一步</button>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { subject, checkAnswer } from '@/common/http.api.js';
	export default{
		data() {
			return {
				rateShow: false, // 倍速浮层
				currentRate: 1.0, //默认1倍速
				isSee:false, //解析显示状态
				result:[], //答案
				data:{
					answer:'leaders and the'
				}
			}
		},
		onLoad() {
			this.videoContext = uni.createVideoContext('videos')
			this.token = store.state.vuex_token 
			let id = store.state.subject_id //大题ID
			subject({token:this.token,subject_id:id,order:1}).then(res=>{
				console.log(res)
				if(res.data.data.option_answer){
					this.isSee = true
				}
				this.data = res.data.data
				console.log(this.data)
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			// 关闭倍速列表
			closeSpeedList(){
				console.log(this.videoList)
				if(this.videoList==true){
					this.rateShow = false;
					this.videoList = false;
				}
			},
			// 显示倍速浮层
			showSwitchRate(rate) {
				if(this.videoList==true){
					this.rateShow = false;
					this.videoList = false;
				}
				let that = this;
				console.log('rateShow');
				that.rateShow = true;
				setTimeout(()=>{
					this.videoList = true
				},500)
			},
			// 切换倍速
			switchRate(e) {
				let that = this;
				let rate = Number(e.currentTarget.dataset.rate);
				that.currentRate = rate;
				that.rateShow = false;
				this.videoContext.playbackRate(rate);
				console.log(rate)
			},
			// 查看解析
			seeResolution() {
				if(this.result.length==this.data.option.length){
					this.isSee = true
					let answers = []
					let id = store.state.subject_id //大题ID
					this.data.option.map((item,index)=>{
							answers.push({key:item.key, value:item.value, answer:this.result[index]}) 
						})
					let answer = JSON.stringify(answers)
					console.log("-----")
					console.log(answers)
					console.log(answer)
					let params = {
						answer: answer,
						answer_type:this.data.type,
						is_end:this.data.is_end,
						option_id:this.data.id,
						subject_id:id,
						token:this.token
					}
					console.log(params)
					checkAnswer({...params}).then(res=>{
						console.log(res)
						let id = store.state.subject_id //大题ID
						subject({token:this.token,subject_id:id,order:1}).then(res=>{
							console.log(res)
							this.data = res.data.data
							console.log(this.data)
						}).catch(err=>{
							console.log(err)
						})
					}).catch(err=>{
						console.log(err)
					})
				}else{
					this.$u.toast("先尝试一下吧~")
				}
			},
			// 下一步
			toNext() {
				if(this.isSee==true){
					uni.navigateTo({
						url: '/pages/index/learnFour'
					})
				}else{
					this.$u.toast("做完再走~")
				}
			},
			// 去单词本
			toNewWord() {
				uni.navigateTo({
					url: '/pages/index/newWordBook'
				})
			}
		}
	}
</script>

<style lang="scss">
	.body{
		padding-bottom: 40rpx;
		button{
			background: linear-gradient(to right, #ffd23f, #fca51d);
			color: #fff;
			font-size: 32rpx;
			line-height: 88rpx;
			border-radius: 100rpx;
			width: 80%;
		}
		.answer{
			.cell{
				display: flex;
				margin: 0 30rpx;
				margin-bottom: 30rpx;
				align-items: center;
				flex-wrap: wrap;
			}
			.sortInput{
				view{
					width: 15%;
					color: #717171;
				}
				input{
					width: 75%;
				}
			}
			.langInput{
				.trueAnswer{
					margin-left: 30%;
				}
				view{
					width: 30%;
					color: #717171;
				}
				input{
					width: 60%;
				}
			}
			.inputs{
				border: 1rpx solid #e1e1e1;
				line-height: 88rpx;
				height: 88rpx;
				padding-left: 40rpx;
				border-radius: 10rpx;
				color: #000;
				font-weight: 500;
			}
			.input{
				border: 1rpx solid #e1e1e1;
				line-height: 88rpx;
				padding-left: 40rpx;
				border-radius: 10rpx;
				color: #000;
				font-weight: 500;
			}
			.falseAnswer{
				color: #ed6c3b;
				border: 1rpx solid #ed6c3b;
				margin-bottom: 0;
				background-image: url(../../static/langStatic/falseIcon.png);
				background-repeat: no-repeat;
				background-size: 30rpx;
				background-position: 98% center;
			}
			.trueAnswer{				
				width: 100% !important;
				color: #4ec36b !important;
				border: none;
				margin-top: 0;
				margin-left: 15%;
			}
			.trueAnswers{
				color: #4ec36b !important;
				border: 1rpx solid #4ec36b;
				background-image: url(../../static/langStatic/trueIcon.png);
				background-repeat: no-repeat;
				background-size: 30rpx;
				background-position: 98% center;
			}
			button{
				padding: 0;
				color: #ffd942;
				width: 150rpx;
				border: 1px solid #ffd942;
				border-radius: 12rpx;
				font-size: 28rpx;
				line-height: 60rpx;
				background-color: transparent;
				background: none;
				margin-bottom: 40rpx;
			}
		}
		.tips{
			font-size: 20rpx;
			color: #ff0000;
			margin: 30rpx;
		}
		.head{
			padding: 0 20rpx;
			display: flex;
			justify-content: space-between;
			align-items: center;
			.word{
				width: 150rpx;
				position: relative;
				background-color: #fff4c7;
				color: #fcaa20;
				text-align: center;
				font-size: 28rpx;
				line-height: 70rpx;
				border-radius: 100rpx;
				padding-left: 30rpx;
				img{
					position: absolute;
					left: 26rpx;
					top: 16rpx;
					width: 30rpx;
					height: 40rpx;
				}
			}
			.difficulty{
				display: flex;
				view{
					color: #b2b2b2;
					font-size: 28rpx;
					margin-right: 10rpx;
				}
			}
		}
		.sentence{
			margin: 40rpx 30rpx;
			line-height: 68rpx;
		}
		.title{
			margin: 40rpx 30rpx;
			display: flex;
			margin-bottom: 30rpx;
			.number{
				background-color: #797979;
				color: #fff;
				width: 36rpx;
				line-height: 36rpx;
				height: 36rpx;
				text-align: center;
				margin-right: 20rpx;
			}
			.text{
				line-height: 36rpx;
				font-size: 30rpx;
				font-weight: 500;
				letter-spacing: 2rpx;
			}
		}
		.answerAnalysis{
			margin: 30rpx 40rpx;
			.title{
				padding-left: 30rpx;
				background-image: url(../../static/point.png);
				background-repeat: no-repeat;
				background-size: 20rpx;
				margin: 0;
				font-size: 28rpx;
				color: #828282;
				background-position: left center; 
			}
			.content{
				margin: 30rpx 0;
			}
			video{
				width: 98%;
				margin: 30rpx 1%;
				height: 370rpx;
				border-radius: 10rpx;
			}
		}
		
		/* 视频CSS开始 */
		.video-control {
		    background-color: transparent;
		    height: 90rpx;
			border-top-left-radius: 20rpx;
			border-top-right-radius: 20rpx;
		    position: absolute;
		    top: 0;
		    right: 0;
		    width: 200rpx;
		    z-index: 1;
		}
		.video-wrap {
		    position: relative;
		}
		.multi-list.full-screen.vertical {
		    height: 100vh !important;
		    padding: 8vh 0;
		}
		.multi-list.full-screen.horizontal {
		    height: 100vw !important;
		    padding: 8vw 0;
		}
		.multi {
			text-align: center;
		    position: absolute;
		    right: 30rpx;
		    top: 50%;
		    transform: translateY(-50%);
		    z-index: 1;
		    width: 100rpx;
		    color: #fff;
		    padding: 10rpx 0;
		    text-align: center;
		    transition: color ease 0.3s;
		}
		.multi.rate {
		    right: 30rpx;
		}
		.multi-list {
		    position: absolute;
		    height: 100%;
		    width: 0;
		    background-color: rgba(0, 0, 0, 0.65);
		    top: 0;
		    right: 0;
		    transition: width 0.3s ease;
		    z-index: 1;
		    box-sizing: border-box;
		    padding: 50rpx 0;
		}
		.multi-list.rate {
		    padding: 25rpx 0;
		}
		.multi-list.active {
		    width: 300rpx;
		}
		.multi-item {
		    text-align: center;
		    color: #fff;
		    line-height: 80rpx;
		    transition: color ease 0.3s;
		}
		.multi-item.rate {
		    line-height: 70rpx;
		}
		/* 视频CSS结束 */
	}
</style>